@font-face {
    font-family: 'Atteron';
    src: url('../fonts/Atteron.ttf')  format('truetype'); /* Safari, Android, iOS */
    font-display: swap;
}

body {
    background: #e4dccb;
}

h2 {
    font-family: 'Atteron', serif;
    font-size: 1.5rem;
}

.title, ul li {
    font-family: serif;
}

#header {
    border-radius: 50% 50% 0 0;
    background: #FCECDD;
    padding: 50px 50px 0 50px;
    margin-top: 32px;
} 

#content {
    border-radius: 0 0 20px 20px;
    background: #FCECDD;
    padding: 0px 10px 40px 10px;
} 

.leave1 {
    max-width: 200px;
    width: 24%;
    height: 400px;
    background: url("/assets/images/leave1.png") no-repeat left / contain;
    left: 0px;
    top : 8%;
    position:absolute;
}

.leave2 {
    max-width: 200px;
    width: 26%;
    height: 400px;
    background: url("/assets/images/leave2.png") no-repeat right / contain;
    right: 0px;
    top : 5%;
    position:absolute;
}

.leave3 {
    max-width: 100px;
    width: 16%;
    height: 400px;
    background: url("/assets/images/leave3.png") no-repeat left / contain;
    top : 30%;
    left: 0;
    position:absolute;
    opacity: 0.5;
}

.leave4 {
    max-width: 120px;
    width: 16%;
    height: 400px;
    background: url("/assets/images/leave3.png") no-repeat left / contain;
    top : 90%;
    right: 0;
    position:absolute;
    transform: scaleX(-1);
    opacity: 0.5;
}

.leave5 {
    max-width: 130px;
    width: 18%;
    height: 400px;
    background: url("/assets/images/leave2.png") no-repeat left / contain;
    top : 172%;
    left: 0;
    position:absolute;
    transform: scaleX(-1);
    opacity: 0.5;
}

@media (max-width: 800px) {
    .leave1 {
        width: 18%;
    }
    
    .leave2 {
        width: 16%;
    }

    .leave3 {
        width: 22%;
    }
  }
  
.languages {
    margin:auto;
    padding: 20px 0 0 0;
    z-index: 100;
    text-align: center;
}
.languages ul li {
    display: inline;
}
.languages ul li a {
    text-decoration: none;
    font-size: 40px; /* Adjust the font size as needed */
    margin-left: 10px; /* Adjust the margin as needed */
}

.languages ul li a {
    opacity: 0.5; /* Adjust the opacity for the inactive language link */
}

.languages a.selected, .languages a:hover {
    opacity: 1; /* Adjust the opacity for the active language link */
}